* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: url(/react-shoppingList/static/media/bg.8455e429.jpg) center;
  background-size: cover;
  background-position: fixed;
  height: 100vh;
  color: white;
}

html {
  font-family: 'Roboto', sans-serif;
}
.header {
    background-color: #20232A;
    color: white;
    text-align: center;
    padding: 20px;
    font-size: 16px;
    letter-spacing: 2px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.header__btn {
    text-transform: uppercase;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 15px;
    border: 2px solid white;
    background-color: transparent;
    color: white;
    font-weight: bold;
    transition: .5s;
    cursor: pointer;
}

@media(min-width: 1024px) {

    .header__btn:hover {
        background-color: #fff;
        color: #20232A;
    }
}
.addItem {
    max-width: 800px;
    margin: 10px auto;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 15px black;
    text-align: center;
}

.addItem__title {
    text-align: center;
    padding: 10px;
    font-size: 2rem;
}

.form {
    display: flex;
    flex-direction: column;
    text-align: center;
}

form label {
    padding: 5px;
}

form input {
    padding: 5px;
    display: block;
    margin: 5px auto;
    padding: 10px;
    font-size: 18px;
    border: none;
    color: white;
    background-color: transparent;
    border-bottom: 2px solid white;
}

form input[type="checkbox"] {
    display: inline-block;
    margin-left: 10px;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

form input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.8);
}

form input::-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.8);
}

form input::placeholder {
    color: rgba(255, 255, 255, 0.8);
}

.form__btn,
.reset__btn {
    margin: 5px auto;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 15px;
    border: 2px solid white;
    background-color: transparent;
    color: white;
    font-weight: bold;
    transition: .5s;
    cursor: pointer;
}


.error__msg {
    text-shadow: 0 0 15px red;
}


@media(min-width: 1024px) {

    .form__btn:hover,
    .reset__btn:hover {
        background-color: #fff;
        color: #20232A;
    }
}
.shopping-list {
    padding: 20px;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 15px black;
    text-align: center;
}

.shopping__title {
    text-align: left;
}
li {
    margin: 20px 5px 10px 5px;
    text-align: center;
    list-style-type: none;
    position: relative;
}

li:after {
    content: '';
    position: absolute;
    bottom: -15%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: rgba(255, 255, 255, 0.2);

}

.important {
    color: rgb(255, 206, 47);
    font-size: 34px;
    position: relative;
    z-index: 12;
}

.important::before {
    content: '!! ';
}

.important::after {
    content: ' !!';
}


button {
    text-transform: uppercase;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 15px;
    border: 2px solid white;
    background-color: transparent;
    color: white;
    font-weight: bold;
    transition: .5s;
    cursor: pointer;
}

@media(min-width: 1024px) {
    button:hover {
        background-color: #fff;
        color: #20232A;
    }
}
.bag-list {
    padding: 20px;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 15px black;
    text-align: center;
}

.bag__title {
    text-align: left;
}
.list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    max-width: 800px;
    margin: 0 auto;
}
